import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/Index';
const NewsDetail = () => import('@/components/NewsDetail');

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Index',
            component: Index,
            meta: {
                keepAlive: true,
                scrollTop: 0,
            }
        },
        {
            path: '/NewsDetail',
            name: 'NewsDetail',
            component: NewsDetail
        },
    ]
})



