<template>
    <div>
        <mt-header fixed title="纯净的知乎"></mt-header>

        <div id="container" class="index-container" v-infinite-scroll="getBeforeNews" infinite-scroll-disabled="loading" infinite-scroll-immediate-check="false" infinite-scroll-distance="10">
            <mt-loadmore :top-method="loadTop" ref="loadmore">
                <div @click="showNewsDetail(story)" class="stories-list" v-for="story in stories">
                    <img class="image" :src="story.images[0]"/>
                    <div class="title">{{story.title}}</div>
                </div>
                <div class="loading"><mt-spinner type="snake" color="#26a2ff"></mt-spinner></div>
            </mt-loadmore>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
    import _ from 'lodash/core';
    import axios from 'axios';
    import { ZHIHU_API_HOST } from '../config';

    export default {
        name: 'Index',
        data() {
            return {
                loading: false,
                top_stories: [],
                stories: [],
                date: '',
            }
        },
        mounted() {
            this.getLatestNews();
        },
        activated() {
            document.getElementById('container').scrollTop = this.$route.meta.scrollTop;
        },
        beforeRouteLeave(to, from, next){
            document.getElementById('container').scrollTop;
            from.meta.scrollTop = document.getElementById('container').scrollTop;
            next();
        },
        methods: {
            loadTop() {
                this.getLatestNews().then(() => {
                    this.$refs.loadmore.onTopLoaded();
                });
            },
            getLatestNews() {
                return new Promise((resolve, reject) => {
                    axios.get(`${ZHIHU_API_HOST}/news/latest`).then((response) => {
                        if (response.status === 200) {
                            const { top_stories, stories, date } = response.data;
                            this.top_stories = top_stories;
                            this.date =  parseInt(date) - 1;
                            stories.forEach((story) => {
                                if (story.images) {
                                    story.images[0] = this.parseImageUrl(story.images[0]);
                                }
                            });
                            this.stories = stories;

                            if (stories.length < 10) {
                                this.getBeforeNews();
                            }

                            resolve();
                        }
                    }).catch((error) => {
                        // console.log(error.response.data.error.message);
                        Toast(error.response.data.error.message);
                        reject();
                    });
                });
            },
            getBeforeNews() {
                this.loading = true;
                const date = parseInt(this.date);
                axios.get(`${ZHIHU_API_HOST}/news/before/${date}`).then((response) => {
                    if (response.status === 200) {
                        const { stories, date } = response.data;
                        this.date = date;
                        stories.forEach((story) => {
                            if (story.images) {
                                story.images[0] = this.parseImageUrl(story.images[0]);
                            }
                        });
                        this.stories = this.stories.concat(stories);
                        this.loading = false;
                    }
                }).catch((error) => {
                    console.log(error);
                    Toast(error.response.data.error.message);
                });
            },
            parseImageUrl(url) {
                if (url && _.isString(url)) {
                    return `${ZHIHU_API_HOST}/simulateRefer?image=${url}`
                }
            },
            showNewsDetail(news) {
                this.$router.push({
                    name: 'NewsDetail',
                    params: {
                        news,
                    },
                });
            },
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .index-container{position: fixed;left: 0;top:40px;right: 0;bottom: 0;overflow: auto;background: #f8f8f8}
    .stories-list{height: 80px;margin: 10px 0 0 0;overflow: hidden;display: flex;align-items: center;box-shadow: 0 1px 5px rgba(0,0,0,0.1);background: #fff;padding: 0 3%;}
    .stories-list .image{width: 30%;height: 70px;display: block;border-radius: 4px;}
    .stories-list .title{flex: 1;margin-left: 15px;}

    .loading{width: 100%;height: 80px;display: flex;align-items: center;justify-content: center;}
</style>



